// Flexbox display
.display-flex {
  @include display-flex;
}
// Inline flex display
.display-inline-flex {
  @include display-inline-flex;
}
// sticky element
.sticky {
  position: -webkit-sticky !important;
  position: -moz-sticky !important;
  position: -ms-sticky !important;
  position: -o-sticky !important;
  position: sticky !important;
  top: 80px;
}

// transition-slow
.transition-slow {
  -webkit-transition: all 0.5s linear;
  -moz-transition: all 0.5s linear;
  -ms-transition: all 0.5s linear;
  -o-transition: all 0.5s linear;
  transition: all 0.5s linear;
}
//rotate
.rotate {
  &45 {
    @include transform(rotate(45deg));
  }
// center item
}
.item-center {
  @include display-inline-flex;
  @include justify-content(center);
  @include align-items(center);
  &-left {
    @include display-inline-flex;
    @include justify-content(center);
    @include align-items(flex-start);
  }
//box shadow
}
.box {
  &-shadow {
    @include box-shadow(0px 0px 15px rgba(41, 41, 42, 0.1));
  }
}
.absolute-center {
  @extend %absolute;
  top: 50%;
  left: 50%;
  @include transform(translate(-50%,-50%));
}
.check {
  @extend %relative;
  &:after {
    @extend %absolute;
    @extend %content;
    display: block;
    width: 8px;
    height: 16px;
    border: solid var(--primary-color);
    border-width: 0 2px 2px 0;
    left: -12px;
    top: 11px;
    margin-top: -10px;
    @include transform(rotate(45deg));
}
}
.times {
  @extend %relative;
    &:after,
    &:before {
      @extend %absolute;
      @extend %content;
      top: 5px;
      left: -11px;
      height: 20px;
      width: 2px;
      background-color: $secondary-100;
  }
  &:before {
    @include transform(rotate(45deg));
  }
  &:after {
    @include transform(rotate(-45deg));
  }
}
.stretched-link {
  &:before {
      @extend %content;
      @extend %absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      pointer-events: auto;
      z-index: 1;
  }

// arrow down
}
.arrow {
  &-up {
    @extend %relative;
    @include transform(rotate(225deg));
    width: 33px;
    height: 33px;
    .close-btn & {
      width: 27.5px;
      height: 30px;
    }
    .hide .close-btn & {
      height: 24px;
    }
    &::after {
      @extend %content;
      top: 45%;
      left: 45%;
      @include transform(translate(-50%, -50%));
      @include transition(all 0.2s linear);
      .close-btn & {
        left: 48%;
        top: 48%;
      }
    }
  }
  &-down {
    @extend %relative;
    width: 16px;
    &::after {
      @extend %content;
      top: 5px;
      left: 5px;
      @include transition(all 0.2s linear);
      @include transform(rotate(225deg));
    }
  }
  &-left {
    @extend %relative;
    width: 16px;
    &::after {
      @extend %content;
      top: 5px;
      left: 5px;
      @include transition(all 0.2s linear);
      @include transform(rotate(225deg));
    }
  }
  &-right {
    @extend %relative;
    width: 16px;
    &::after {
      @extend %content;
      top: 9px;
      left: 7px;
      @include transition(all 0.2s linear);
      @include transform(rotate(135deg));
    }
  }
  &-fill {
    &-right {
      @extend %relative;
      width: 16px;
    }
  }
}